@import url(https://fonts.googleapis.com/css?family=Playfair+Display:400,500,700,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navbar-wrap{position:fixed;top:0;width:100vw;box-shadow:0 1px 10px 0 rgba(0,0,0,0.1)}.navbar-wrap .desk-wrap{display:flex;justify-content:space-between;align-items:center;padding:40px 90px}.navbar-wrap .desk-wrap .right-panel .menu-items-wrap{display:flex}.navbar-wrap .desk-wrap .right-panel .menu-items-wrap .menu-item-wrap{margin-right:16px}

.hero-wrap{height:100vh;background-color:black;background-image:url(/hch2904.githu.io/static/media/hero-bg.fd88e2ed.png);background-size:cover;background-position:center}.hero-wrap ._main-content-wrap{padding-top:134px;display:flex;max-width:960px;margin:0 auto}.hero-wrap ._main-content-wrap .img-name-wrap,.hero-wrap ._main-content-wrap .my-info-wrap{flex:1 1}.hero-wrap ._main-content-wrap .img-name-wrap{flex:2 1;display:flex;justify-content:flex-end;position:relative;max-height:calc(100vh - 134px);width:100%}.hero-wrap ._main-content-wrap .img-name-wrap img{height:100%;width:100%;max-width:300px}.hero-wrap ._main-content-wrap .img-name-wrap .name-wrap{position:absolute;top:220px;left:0;color:white;font-size:42px}.hero-wrap ._main-content-wrap .img-name-wrap .name-wrap .fname,.hero-wrap ._main-content-wrap .img-name-wrap .name-wrap .lname{margin:0}.hero-wrap ._main-content-wrap .img-name-wrap .name-wrap .lname{line-height:1.1}.hero-wrap ._main-content-wrap .img-name-wrap .name-wrap .fake-border{margin:0;margin-top:16px;color:#F26C4F;border-color:#F26C4F;max-width:220px}.hero-wrap ._main-content-wrap .my-info-wrap{align-self:center;color:white}.hero-wrap ._main-content-wrap .my-info-wrap .role-name{color:#F26C4F;margin-bottom:8px;border-bottom:1px solid #F26C4F}.hero-wrap ._main-content-wrap .my-info-wrap .location-info-1,.hero-wrap ._main-content-wrap .my-info-wrap .location-info-2{margin:0}.hero-wrap ._main-content-wrap .my-info-wrap p{font-size:15px}.hero-wrap ._main-content-wrap .my-info-wrap .contact-email{color:#F26C4F}

.hch-btn-primary{height:32px;background-color:#222;border:1px solid white;color:white;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;padding:0 8px;font-size:14px;min-width:90px;border-radius:10px;transition:background-color 300ms ease-in-out;cursor:pointer}.hch-btn-primary:hover,.hch-btn-primary:active{background-color:#333;border-color:#F26C4F}body,button{font-family:'Playfair Display', serif}.App-header{color:red}

